<template>
    <div class="home">
        <v-layout row>
            <v-flex xs12 class="vault-title-block">
                <h3>{{activeVault.name}}</h3>
            </v-flex>
        </v-layout>

        <v-container fluid grid-list-md class="transparent">

            <v-layout row wrap>


                <!-- load results -->
                <v-flex v-bind="{ [`xs4`]: true }" v-for="card in vaultKeeps" :key="card.title">

                    <v-card>
                        <!-- picture & Title -->
                        <v-card-media :src="card.imgUrl" height="200px" @click.stop="expandKeep(card)">
                            <v-container fill-height fluid>
                                <v-layout fill-height>
                                    <v-flex xs12 align-end flexbox>
                                        <span class="headline white--text" v-text="card.title"></span>
                                    </v-flex>
                                </v-layout>
                            </v-container>
                        </v-card-media>

                        <!-- button row -->
                        <v-card-actions class="white">
                            <v-spacer></v-spacer>
                            <v-btn icon>
                                <v-icon class="grey--text">favorite</v-icon>
                            </v-btn>
                            <v-btn icon>
                                <v-icon class="grey--text">bookmark</v-icon>
                            </v-btn>
                            <v-btn icon>
                                <v-icon class="grey--text">share</v-icon>
                            </v-btn>
                        </v-card-actions>

                        <!-- description and counter displays -->
                        <v-card-text>
                            <span class="white--text" v-text="card.description"></span>
                        </v-card-text>
                        <v-flex>
                            <v-icon class="grey--text bottom-icons">remove_red_eye</v-icon><span class="grey--text" v-text="card.views.length"></span>
                            <v-icon class="grey--text bottom-icons">bookmark</v-icon><span class="grey--text" v-text="card.saves"></span>
                        </v-flex>
                    </v-card>

                </v-flex>
                <!-- end load results -->

                <!-- Modal (expand keep) -->
                <v-dialog v-model="dialog" lazy absolute :width="viewWidth">
                    <v-card>
                        <!-- picture & Title -->
                        <v-card-media class="modal-image" :src="activeKeep.imgUrl">
                            <v-container fill-height fluid>
                                <v-layout fill-height>
                                    <v-flex xs12 align-end flexbox>
                                        <span class="headline white--text" v-text="activeKeep.title"></span>
                                    </v-flex>
                                </v-layout>
                            </v-container>
                        </v-card-media>

                        <!-- button row -->
                        <v-card-actions class="white">
                            <v-spacer></v-spacer>
                            <v-btn icon>
                                <v-icon class="grey--text">favorite</v-icon>
                            </v-btn>
                            <v-btn icon>
                                <v-icon class="grey--text">bookmark</v-icon>
                            </v-btn>
                            <v-btn icon>
                                <v-icon class="grey--text">share</v-icon>
                            </v-btn>
                        </v-card-actions>

                        <!-- description and counter displays -->
                        <v-card-text>
                            <span class="white--text" v-text="activeKeep.description"></span>
                        </v-card-text>
                        <v-flex>
                            <v-icon class="grey--text bottom-icons">remove_red_eye</v-icon><span class="grey--text" v-text="activeKeep.views"></span>
                            <v-icon class="grey--text bottom-icons">bookmark</v-icon><span class="grey--text" v-text="activeKeep.saves"></span>
                        </v-flex>
                    </v-card>
                </v-dialog>


            </v-layout>
        </v-container>
    </div>
</template>

<script>
    function CalculateModalW() {
        var vw = Math.max(document.documentElement.clientWidth, window.innerWidth)
        if (vw <= 600) {
            return "90%"
        }
        else if (600 < vw && vw <= 768) {
            var width = (-45 / 302) * vw + (25140 / 151)
            return width.toString() + "%";
        }
        else if (768 < vw && vw <= 1264) {
            var width = (-35 / 302) * vw + (25140 / 151)
            return width.toString() + "%";
        }
        else if (vw > 1264) {
            return "25%"
        }
    }


    export default {
        name: 'home',
        data() {
            return {
                viewWidth: CalculateModalW(),
                activeKeep: {},
                msg: 'Welcome to Your Vue.js App',
                dialog: false
            }
        },
        methods: {
            expandKeep(keep) {
                this.activeKeep = keep;
                this.dialog = true;
            }
        },
        computed: {
            vaultKeeps() {
                return this.$store.state.vaultKeeps;
            },
            activeVault() {
                return this.$store.state.activeVault;
            }
        },
        beforeMount() {
            this.$store.dispatch('clearActiveVault')
        },
        mounted() {
            this.$store.dispatch('GetVault', this.$route.params.vaultId)
        }
    }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .modal-image {
        height: 50vh !important;
    }

    .vault-title-block {
        display: flex;
        justify-content: center;
    }

    h1,
    h2 {
        font-weight: normal;
    }

    ul {
        list-style-type: none;
        padding: 0;
    }

    li {
        display: inline-block;
        margin: 0 10px;
    }

    a {
        color: #42b983;
    }

    .bottom-icons {
        margin-left: 15px;
        margin-bottom: 10px;
    }
</style>


